.page-main__slogan {
  min-width: 320px;
  background: url("../img/bg-back-mobile@1x.jpg") 0 0 no-repeat;
  background-size: cover;

  &--toggle-open {
    background: url("../img/bg-back-mobile@1x.jpg") 0 23% no-repeat;
    background-size: cover;
  }
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .page-main__slogan {
    background: url("../img/bg-back-mobile@2x.jpg") 0 0 no-repeat;
    background-size: cover;

    &--toggle-open {
      background: url("../img/bg-back-mobile@1x.jpg") 0 23% no-repeat;
      background-size: cover;
    }
  }
}

.page-main__slogan-text {
  width: 230px;
  margin: 0 auto;
  .setFont(white, 24px, @light);
  text-align: center;
  line-height: 30px;
  padding-top: 95px;
  padding-bottom: 51px;

  &--photo {
    padding-bottom: 80px;
  }

  &--toggle-open {
    padding-top: 29px;
  }
}

@media (min-width: 660px) {
  .page-main__slogan {
    margin: 0;
    padding: 0;
    min-width: 660px;
    background: url("../img/bg-back-tablet@1x.jpg") no-repeat;
    background-size: cover;
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-main__slogan {
      background: url("../img/bg-back-tablet@2x.jpg") no-repeat;
      background-size: cover;
    }
  }

  .page-main__slogan-text {
    width: 600px;
    padding: 0;
    .setFont(white, 50px, @light);
    line-height: 60px;
    text-align: center;
    padding-top: 180px;
    padding-bottom: 81px;

    &--photo {
      padding-top: 180px;
      padding-bottom: 142px;
    }

    &--toggle-open {
      padding-top: 71px;
    }
  }
}

@media (min-width: 960px) {
  .main-wrapper {
    width: 960px;
    margin: 0 auto;
  }

  .page-main__slogan {
    margin: 0;
    padding: 0;
    background: url("../img/bg-back-desktop@1x.jpg") 50% 0% no-repeat;
    background-size: 200%;
  }

  @media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
    .page-main__slogan {
      background: url("../img/bg-back-desktop@2x.jpg") 50% 0 no-repeat;
      background-size: 200%;
    }
  }

  .page-main__slogan-text {
    width: 960px;
    line-height: 60px;
    .setFont(white, 50px, @light);
    padding-top: 173px;
    padding-bottom: 100px;

    &--photo {
      width: 370px;
    }
  }
}
